import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'IndexPage',
    redirect: '/chartpage',
    component: () => import(/* webpackChunkName: "about" */ '../views/IndexPage.vue'),
  },
  {
    path: '/indexpage',
    name: 'IndexPage',
    component: () => import(/* webpackChunkName: "about" */ '../views/IndexPage.vue'),
    children: [
      {
        path: '/chartpage',
        name: 'ChartPage',
        component: () => import(/* webpackChunkName: "about" */ '../views/ChartPage.vue'),
        children:[
          {
            path: 'codepage/:id',
            name: 'CodePage',
            component: () => import(/* webpackChunkName: "about" */ '../views/CodePage.vue'),
          }
        ]
      },
      
      {
        path: '/toolspage',
        name: 'ToolsPage',
        redirect: '/onlinepage',
        component: () => import(/* webpackChunkName: "about" */ '../views/ToolsPage.vue'),
        children: [
          {
            path: '/onlinepage',
            name: 'OnlinePage',
            component: () => import(/* webpackChunkName: "about" */ '../views/OnlinePage.vue'),
          },
          {
            path: '/onlinepage1',
            name: 'OnlinePage1',
            component: () => import(/* webpackChunkName: "about" */ '../views/OnlinePage1.vue'),
          }
        ]
      }
    ]
  },
  {
    path: 'testchart',
    name: 'TestChart',
    component: () => import(/* webpackChunkName: "about" */ '../views/TestChart.vue'),
  }


]

const router = new VueRouter({
  routes
})

export default router
