import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
// chart相关
import barDemoJS from '../../public/js/bar.js'
import lineDemoJS from '../../public/js/line.js'
import pieDemoJS from '../../public/js/pie.js'
import mapDemoJS from '../../public/js/map.js'
import radarDemoJS from '../../public/js/radar.js'
import guageDemoJS from '../../public/js/guage.js'
export default new Vuex.Store({
  state: {
    //默认路径默认列表
    currentPath: 'bar',
    currentList: barDemoJS,
    barDemoJS: barDemoJS,
    lineDemoJS: lineDemoJS,
    pieDemoJS: pieDemoJS,
    mapDemoJS: mapDemoJS,
    radarDemoJS: radarDemoJS,
    guageDemoJS: guageDemoJS,
  },
  getters: {
    //根据id获取详情，渲染codepage
    getChartById: (state) => (id) => {
      return state[state.currentPath + 'DemoJS'].find(item => item.id === id)
    }
  },
  mutations: {
    //更改当前列表，渲染mainbody
    updateCurrent: (state, payload) => {
      state.currentPath = payload
      state.currentList = state[state.currentPath + 'DemoJS']
    }
  },
  actions: {
  },
  modules: {
  }
})
